<template>
  <el-row :gutter="20">
    <el-col :span="16">
      <div class="grid-content">
        <v-container class="box">
          <v-row class="mt-10"></v-row>
          <h1>聚合报告详情</h1>
          <h2>聚合报告ID：{{reportInfo.id}}</h2>
          <v-row
              align="center"
              justify="end"
          >
            报告审核情况：<el-tag>报告审核情况</el-tag>
          </v-row>

          <el-divider></el-divider>

          <p>
            <v-row class="d-flex justify-space-between">
              <v-col>
                创建时间：{{}}
              </v-col>
              <v-col>
                BUG严重程度：{{}}
              </v-col>
            </v-row>
          </p>
          <p>
            BUG分类：{{}}
          </p>
          <p>
            审核人：<el-tag>{{}}</el-tag>
          </p>

          <el-divider></el-divider>

          <div>
            <p>
              主要点：{{}}
            </p>
            <p>
              <img :src="$host+reportInfo.picture+'?newName='+reportInfo.picture" width="100%"/>
            </p>
          </div>

          <el-divider></el-divider>

          <h4>补充点</h4>

          <div
              class="report-area"
              v-for="(report, reportIndex) in reports"
              :key="reportIndex"
          >
            <v-card class="report">
              <h5>{{ report.name }}</h5>
              <p>{{report.content }}</p>
              <img :src="$host+report.picture+'?newName='+report.picture" width="100%"/>
              <span class="comment-trigger" @click="detailedReport(reportIndex)">回复</span>
            </v-card>
          </div>

          <v-row class="ma-16"></v-row>

        </v-container>
      </div>
    </el-col>

    <!--  有点想在中间加一个分割线 但是没有找到正确的方法  -->

    <el-col :span="8">
      <div class="grid-content">
        <v-container class="box">
          <v-row class="mt-10"></v-row>
          <p>需不需要加一个词云图片？</p>
          <img :src="$host+reportInfo.picture+'?newName='+reportInfo.picture" width="100%"/>

          <el-divider></el-divider>

          <h2>报告关系展示</h2>
          <el-tree
              :data="data"
              :props="defaultProps"
              @node-click="handleNodeClick"
          ></el-tree>
        </v-container>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import {getReportCluster} from "@/api/report";

export default {
  name: "AggregateReportDetails",
  data() {
    return {
      reportInfo:[],
      data: [{
        label: '一级 1',
        children: [{
          label: '二级 1-1',
          children: [{
            label: '三级 1-1-1'
          }]
        }]
      }, {
        label: '一级 2',
        children: [{
          label: '二级 2-1',
          children: [{
            label: '三级 2-1-1'
          }]
        }, {
          label: '二级 2-2',
          children: [{
            label: '三级 2-2-1'
          }]
        }]
      }, {
        label: '一级 3',
        children: [{
          label: '二级 3-1',
          children: [{
            label: '三级 3-1-1'
          }]
        }, {
          label: '二级 3-2',
          children: [{
            label: '三级 3-2-1'
          }]
        }]
      }],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
    };
  },

  methods:{
    handleNodeClick(data) {
      console.log(data);
    },

    loadReport() {
      // id: this.$route.params.reportId
      getReportCluster({ id: 1 }).then(res => {
        console.log(res)
        this.reportInfo = res.data;
        //this.reportInfo.picture="bb703866-038f-4937-ad75-1e0e19b0b1df.png";
        console.log(res.data);
      });
    },
  },

  mounted() {
    this.loadReport();
  }
};
</script>

<style scoped>
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.box {
  width: 80%;
}
.topcss {
  margin-top: 50px;
  border: skyblue 1px solid;
  box-shadow: 2px 2px 2px 2px #44b7ff;
}
.comment-edit {
  margin-top: 5px;
  padding: 10px;
  width: 100%;
  height: 200px;
  background-color: white;
  color: gray;
  border: skyblue 1px solid;
  border-radius: 3px;
  box-shadow: 2px 2px 2px 2px lightskyblue;
  resize: none;
  outline: none;
  cursor: text;
}
</style>