<template>
  <v-card color="indigo lighten-5" width="100%" height="100%" class="ma-4 pa-2">
    <div class="left">
    <v-card-title class="headline">
      {{ taskName }}
    </v-card-title>

    <v-card-text class="text">
      【{{ type }}】
      {{ text }}
    </v-card-text>
    </div>

    <div class="right">
    <v-card-actions>
      <v-row
          align="center"
          justify="space-around"
          class="top"
      >
        <v-btn dark color="indigo" @click="handleReport">
          查看报告
        </v-btn>
      </v-row>
    </v-card-actions>
    </div>
  </v-card>
</template>

<script lang="ts">
import Vue from "vue";

export default {
  name: "TaskItemEmployer",
  props: {
    taskName: {
      type: String,
      default: "taskName"
    },
    taskId: {
      type: Number,
      default: 0
    },
    description: {
      type: String,
      default: "任务简介"
    },
    type: {
      type: String,
      default: "功能测试"
    },
    requiredNumber:{
      type: Number,
      default: 0
    },
    status:{
      type: Number,
      default: 4
    },
    accepted: {
      type: Boolean,
      default: false
    },
    manageable: {
      type: Boolean,
      default: false
    },
  },

  methods:{
    // 查看报告的函数 需要修改链接
    handleReport() {
      this.$router.push(`/employer/postarea/${this.taskId}/1`);
    },
  },

  computed: {
    text: function() {
      return this.description.length < 60
          ? this.description
          : this.description.substring(0, 60) + "...";
    },

  }
}
</script>

<style scoped>
.text {
  height: 40px;
  overflow: hidden;
}
.left{
  float: left;
  /*margin-bottom: 5%;*/
}
.right {
  float: right;
}
.top {
  float: top;
  margin-right: 20%;
  margin-top: 40%;
}
</style>
