  padding: 3px;
  margin-top: 30px;
  height: 100%;
}
.divider{
  margin-top: 5px;
  width: 100%;
  border: #000b82;
  box-shadow: 2px 2px 2px 2px #000b82;
}
.aggregateReport-trigger {
  cursor: pointer;
  color: #000b82;
  margin-left: 10px;
}
</style>